import React from 'react';

const AboutPage = () => (
    <>
    <h1>About me.</h1>
    <p>
        Writers are the people who produce written material to communicate ideas.
        Writing encompasses several genres and writers may write for occupational purposes or as an outlet for their creativity.
    </p>
    <p>
        Professional writers include screenwriters, content writers, journalists, etc. whose job entails writing scripts, web content and news reports. 
        Others like short story writers and novelists often begin writing as hobby before converting it to a full time profession.
        James Howell, a 17th century historian cum writer was the first writer to earn his living from writing alone in the English language; other writers of those times pursued writing as a hobby, not a profession as writing alone never paid enough.
    </p>
    </>
);

export default AboutPage;